@import '../../assets/sass/constants.scss';

.faq {
    max-width: $max-width;
    margin: 0 auto;
    width: 100%;
    padding: 50px 100px 0;

    @media(max-width: $breakpoint) {
        padding: 0 20px;
    }


    h4 {
        padding: 10px 0;
        padding-left: 20px;
        border-left: 4px solid $orange;
        font-size: 26px;
        color: $blue;
        line-height: 30px;
        margin-bottom: 50px;

        small {
            font-size: 18px;
            display: block;
            color: $red;
        }
    }
}


.questao{
    width: 100%;
    margin-bottom: 20px;

    .header{
        background-color: $orange;
        display: flex;
        color: $black;
        padding: 14px;
        justify-content: space-between;
        align-items: center;

        svg{
            font-size: 30px;
            width: 40px;
        }

        h5{
            font-size: 16px;
        }
    }

    .body{
        background-color: $vanilla;
        color: $black;
        padding: 14px;

        p{
            font-size: 14px;
        }
    }
}