@import '../../assets/sass/constants.scss';

.contato {
    max-width: $max-width;
    margin: 0 auto;
    width: 100%;
    padding: 50px 100px;
    display: flex;
    align-items: center;

    @media(max-width: $breakpoint) {
        padding: 50px 20px;
        flex-direction: column-reverse;
    }


    .content {
        margin-left: 50px;
        @media(max-width: $breakpoint) {
            width: 100%;
            margin-left: 0;
            margin-bottom: 50px;
        }

        h4 {
            padding: 10px 0;
            padding-left: 20px;
            border-left: 4px solid $orange;
            font-size: 26px;
            color: $blue;
            line-height: 30px;
            margin-bottom: 50px;

            small {
                font-size: 18px;
                display: block;
                color: $red;
            }
        }

        p{
            margin-bottom: 10px;
        }

        .whatsapp{
            background-color: $orange;
            color: $black;
            width: fit-content;
            display: block;
            border: none;
            font-size: 14px;
            text-decoration: none;
            padding: 8px 20px;
            font-weight: bold;
            border-radius: 5px;
            margin-top: auto;
            cursor: pointer;
        }
    }
}