@import '../../assets/sass/constants.scss';

.vantagens {
    max-width: $max-width;
    margin: 0 auto;
    width: 100%;
    padding: 50px 100px 0;
    display: grid;
    grid-template-columns: 22% 22% 22% 22%;
    justify-content: space-between;

    @media(max-width: $breakpoint) {
        grid-template-columns: 100%;
        padding: 50px 20px;

        .vantagem{
            margin-bottom: 50px;
        }
    }

    h2 {
        color: $red;
        margin-bottom: 20px;
    }


    .vantagem {
        display: flex;
        flex-direction: column;
        align-items: center;
        text-align: center;

        svg {
            font-size: 150px;
            margin-bottom: 20px;
            color: $orange;
        }

        h3 {
            font-size: 20px;
            line-height: 30px;
            min-height: 60px;
            margin-bottom: 10px;
            color: $blue;
        }

        p {
            text-align: justify;
        }
    }

}