@import '../../assets/sass/constants.scss';

.professor {
    max-width: $max-width;
    margin: 0 auto;
    width: 100%;
    display: flex;
    padding: 50px 100px 0;
    align-items: center;

    @media(max-width: $breakpoint) {
        flex-direction: column;
        padding: 0 20px 50px;
    }

    img {
        width: 400px;
        object-fit: cover;
        margin-right: 50px;

        @media(max-width: $breakpoint) {
            margin-right: 0px;
            margin-bottom: 50px;
        }
    }

    .content {
        h4 {
            padding: 10px 0;
            padding-left: 20px;
            border-left: 4px solid $orange;
            font-size: 26px;
            color: $blue;
            line-height: 30px;
            margin-bottom: 50px;

            small {
                font-size: 18px;
                display: block;
                color: $red;
            }
        }

        p {
            font-size: 18px;
            line-height: 28px;
            margin-top: 20px;
        }
    }
}