@import '../../assets/sass/constants.scss';

.flag-red {
    width: 100%;
    height: 100px;
    background-color: $red;
}

.flag-content {
    width: 100%;
    background-color: $yellow;

    .content {
        max-width: $max-width;
        margin: 0 auto;
        width: 100%;
        display: flex;
        justify-content: space-between;
        align-items: stretch;
        padding: 0 100px;

        @media(max-width: $breakpoint) {
            justify-content: center;
            padding: 0 50px;
        }

        img {
            max-width: 60%;

            @media(max-width: $breakpoint) {
                display: none;
            }
        }

        .ballon {
            position: absolute;
            width: 150px;
            text-align: center;
            background-color: $blue;
            color: $white;
            height: 150px;
            padding: 15px;
            border-radius: 50%;
            display: flex;
            align-items: center;
            justify-content: center;
            transform: translateY(-50px);
            font-weight: 500;

            @media(max-width: $breakpoint) {
                display: none;
            }
        }

        .card-form {
            width: 360px;
            background-color: $white;
            transform: scale(1.2);
            box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
            border-radius: 5px;
            padding: 10px 20px;

            h1 {
                font-size: 20px;
                color: $red;
                line-height: 30px;
                margin-bottom: 5px;
            }

            p {
                font-size: 14px;
                margin-bottom: 5px;
            }

            form {
                width: 100%;

                .form-field {
                    width: 100%;
                    margin-bottom: 10px;

                    label {
                        display: block;
                        margin-bottom: 3px;
                        font-weight: bold;
                        font-size: 12px;
                    }

                    input {
                        width: 100%;
                        display: block;
                        font-size: 14px;
                        padding: 4px 8px;
                    }
                }

                .form-checkbox-field {
                    margin-bottom: 10px;
                    display: flex;
                    align-items: center;

                    label {
                        margin-left: 10px;
                        font-size: 12px;
                    }

                }

                .form-button {
                    background-color: $orange;
                    display: block;
                    width: 100%;
                    border: none;
                    font-size: 14px;
                    padding: 8px;
                    font-weight: bold;
                    border-radius: 5px;
                    margin-top: auto;
                    cursor: pointer;
                }
            }
        }
    }
}